import React from 'react';
import {useSelector} from "react-redux";
import {Redirect} from 'react-router-dom'
import DrawerComponent from "../Component/Drawer.Component";
import {Switch, Route} from "react-router-dom";
import Loadable from 'react-loadable'
import LoadingComponent from "../Component/Loading.Component";
import BannerPage from "./BannerPage";
import NewsPage from "./NewsPage";
import EditNews from "./EditNews";
import AllNews from "./AllNews";

const Product = Loadable(
    {
        loader: () => import('./Product'),
        loading: LoadingComponent,
    })
const AddProductPage = Loadable(
    {
        loader: () => import('./AddProductPage'),
        loading: LoadingComponent,
        delay: 5000
    })
const ParCategoryPage = Loadable(
    {
        loader: () => import('./ParCategoryPage'),
        loading: LoadingComponent,
        delay: 5000
    })
const CategoryPage = Loadable(
    {
        loader: () => import('./CategoryPage'),
        loading: LoadingComponent
    })
const BranchPage = Loadable(
    {
        loader: () => import('./BranchPage'),
        loading: LoadingComponent
    })
const EditProductPage = Loadable(
    {
        loader: () => import('./EditProductPage'),
        loading: LoadingComponent
    })
const OrdersPage = Loadable({
    loader: () => import('./OrdersPage'),
    loading: LoadingComponent
})
const OrdersSinglePage = Loadable({
    loader: () => import('./OrderSinglePage'),
    loading: LoadingComponent
})
const MainPage = () => {
    const {isAuthenticated} = useSelector(state => state.AuthenReducer)
    return (
        <div>
            {
                !isAuthenticated ?
                    <Redirect to="/login"/>
                    :
                    <div>
                        <DrawerComponent/>
                        <div style={{marginLeft: '240px', padding: '1rem'}}>
                            <Switch>
                                <Route path='/products' component={Product}/>
                                <Route path='/add_product' component={AddProductPage}/>
                                <Route path={'/par_category'} component={ParCategoryPage}/>
                                <Route path={'/category'} component={CategoryPage}/>
                                <Route path={'/branch'} component={BranchPage}/>
                                <Route path={'/edit_product/:product_id'} component={EditProductPage}/>
                                <Route path={'/banner'} component={BannerPage}/>
                                <Route path='/add_news' component={NewsPage}/>
                                <Route path='/edit_news/:news_path' component={EditNews}/>
                                <Route path={'/news'} component={AllNews}/>
                                <Route exact path={'/orders'} component={OrdersPage}/>
                                <Route path={'/orders/:id'} component={OrdersSinglePage} />
                            </Switch>
                        </div>
                    </div>
            }
        </div>
    );
};

export default MainPage;