import React from 'react';
import NewsComponent from "../Component/News/NewsComponent";
import {post_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import {useHistory} from 'react-router-dom'
const NewsPage = () => {
    const history = useHistory()
    const handleSave = (data) => {
        if (data.title === '') {
            alert('You need to fill title')
            return
        }
        post_data(BACK_END_URL + 'admin/news', {}, {
            title: data.title,
            summary: data.summary,
            type_news: data.type_news,
            image: data.image,
            body: data.body,
        }, true)
            .then(res=>{
                alert('Save news successfully!!! Returning...')
                history.push('/news')
            })
            .catch(err => alert('Error when insert data'))
    }
    return (
        <div>
            <h2>Thêm tin tức</h2>
            <NewsComponent data={{
                title: '',
                summary: '',
                type_news: 0,
                image: '',
                body: '',
            }} onSave={handleSave}/>
        </div>
    );
};

export default NewsPage;