import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from "react-router-dom";
import {get_data, post_data, put_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import NewsComponent from "../Component/News/NewsComponent";

const EditNews = () => {
    const history = useHistory()
    const {news_path} = useParams()
    const [state, setState] = useState({
        id : 0,
        title: '',
        summary: '',
        type_news: 0,
        image: '',
        body: '',
    })
    useEffect(() => {
        get_data(BACK_END_URL + `public/news/${news_path}`, {}, false)
            .then(res => setState({
                id: res.data.news.id,
                title: res.data.news.title,
                summary: res.data.news.summary,
                type_news: res.data.news.type_news,
                image: res.data.news.image,
                body: res.data.news.body
            }))
            .catch(error => {
                alert('Fail when load data')
                history.push('/news')
            })

    }, [news_path])
    const handleSave = (data) => {
        if (data.title === '') {
            alert('You need to fill title')
            return
        }
        put_data(BACK_END_URL + `admin/news/${state.id}`, {}, {
            title: data.title,
            summary: data.summary,
            type_news: data.type_news,
            image: data.image,
            body: data.body,
        }, true)
            .then(res => {
                alert('Save news successfully!!! Returning...')
                history.push('/news')
            })
            .catch(err => alert('Error when insert data'))
    }
    return (
        <div>
            <h2>Edit tin tức</h2>
            <NewsComponent data={state} onSave={handleSave}/>
        </div>
    );
};

export default EditNews;