import React, {useEffect, useState} from 'react';
import {TextField, Paper, FormControl, Select, MenuItem, Button} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import {theme} from "../../Themes";
import CkEditorComponent from "../CK_Editor/CkEditor.Component";
import ImageDialog from "../ImageDialog";
import {useHistory} from 'react-router-dom'

const useStyle = makeStyles({
    container: {
        width: '100%',
        margin: 'auto',
        padding: '1em',
        boxSizing: "border-box"
    },
    buttonContainer: {
        width: '100%',
        margin: 'auto',
        marginTop: '1em',
        display: 'flex',
        justifyContent: "flex-end"
    },
    title: {
        width: '100%',
        '&>input': {
            fontSize: '2em'
        }
    },
    summary: {
        width: '100%',
        marginTop: '1em'
    },
    imageContainer: {
        width: '50%',
        margin: 'auto',
        marginTop: '1em',
        position: 'relative',
        paddingTop: '33.33%',
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundPosition: "center"
    },
    innerText: {
        position: 'absolute',
        bottom: 0
    },
    formControl: {
        width: '100%'
    },
    selectType: {
        marginTop: '1em',
        width: '100%'
    },
    labelInput: {
        position: "absolute",
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        border: '1px solid rgba(0,0,0,0.5)',
        padding: '0.5em',
        borderRadius: '5px',
        '&:hover': {
            backgroundColor: theme.palette.primary.main,
            color: 'white',
            cursor: 'pointer'
        }
    },
    upLoad: {
        display: 'none'
    }
})
const NewsComponent = ({data, onSave}) => {
    const classes = useStyle()
    const history = useHistory()
    const [state, setState] = useState({
        title: '',
        summary: '',
        type_news: 0,
        image: '',
    })
    useEffect(() => {
        setState({
            ...state,
            title: data.title,
            summary: data.summary,
            type_news: data.type_news,
            image: data.image,
        })
        setBody(data.body)
    }, [data])
    const [body, setBody] = useState('')
    const handleChange = (e) => {
        let val = e.target.value
        let name = e.target.name
        setState({
            ...state,
            [name]: val
        })
    }
    const handleBodyChange = (e, editor) => {
        const data = editor.getData()
        setBody(data)
    }
    const handleImageChange = (e, name) => {
        const file = e.target.files[0]
        let url = URL.createObjectURL(file)
        setDataImage({
            name: name,
            src: url,
            aspect: 1.5
        })
        setOpenImage(true)
    }
    const handleSetImage = (e => {
        let name = e.name
        let value = e.url_image
        setState({
            ...state,
            image: value
        })
    })
    const handleImageClose = () => {
        setOpenImage(false)
    }
    const [openImage, setOpenImage] = useState(false)
    const [dataImage, setDataImage] = useState({
        name: null,
        src: null,
        aspect: 1.5
    })
    return (
        <section>
            <Paper className={classes.container}>
                <TextField name='title' value={state.title} onChange={handleChange} className={classes.title}
                           placeholder={'Title'} variant={'outlined'}/>
                <FormControl className={classes.formControl}>
                    <Select
                        value={state.type_news}
                        onChange={handleChange}
                        className={classes.selectType}
                        variant={'outlined'}
                        name='type_news'
                    >
                        <MenuItem value={0}>
                            Tin tức
                        </MenuItem>
                        <MenuItem value={1}>Thông tin chung</MenuItem>
                        <MenuItem value={2}>Hỗ trợ</MenuItem>
                    </Select>
                </FormControl>
                <TextField name='summary' value={state.summary} onChange={handleChange} className={classes.summary}
                           multiline={true} rows={4} placeholder={'Summary'} variant={'outlined'}/>
                <div>
                    <div className={classes.imageContainer} style={{
                        border: '1px dashed rgba(0,0,0,0.5)', backgroundImage: `url(${state.image})`
                    }}>
                        <div className={classes.innerText}></div>
                        <label className={classes.labelInput} htmlFor={'upload-file-summary'}>Upload</label>
                        <input id='upload-file-summary' type={'file'} onChange={handleImageChange}
                               className={classes.upLoad}/>
                    </div>
                </div>
            </Paper>
            <Paper className={classes.container} style={{marginTop: '1em'}}>
                <CkEditorComponent onChange={handleBodyChange} data={body}/>
            </Paper>
            <div className={classes.buttonContainer}>
                <Button variant={'contained'} color={'primary'} onClick={() => {
                    onSave({
                        title: state.title,
                        summary: state.summary,
                        type_news: state.type_news,
                        image: state.image,
                        body : body
                    })
                }}>Save</Button>
                <Button variant={'contained'} style={{marginLeft: '1em'}} onClick={() => {
                    history.push('/news')
                }}>Cancel</Button>
            </div>
            <ImageDialog data={dataImage} open={openImage} setPImage={handleSetImage} onClose={handleImageClose}/>
        </section>
    );
};

export default NewsComponent;