import React, {useEffect, useState} from 'react';
import {FormControl, MenuItem, Select, Paper} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import {delete_data, get_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import {useHistory} from 'react-router-dom'
import IsHotNews from "../Component/News/IsHotNews";
import DeleteDialog from "../Component/Dialog.Delete";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';

const useStyle = makeStyles({
    root: {
        width: '90%',
        margin: 'auto'
    },
    newContainer: {
        display: 'grid',
        gridTemplateColumns: '1fr 1fr 1fr 1fr',
        gridGap: '2em',
        marginTop: '1em',
        width: "100%"
    },
    imageContainer: {
        position: 'relative',
        paddingTop: '66.66%',
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundColor: "red"
    },
    elementContainer: {
        '&:hover': {
            cursor: 'pointer'
        }
    },
    innerText: {
        position: 'absolute',
        bottom: 0,
        top: 0,
        left: 0,
        right: 0,
        display: "none"
    },
    wrapper: {
        padding: '1em'
    },
    title: {
        fontSize: '1.3em',
        fontWeight: 'bold'
    },
    summary: {
        marginTop: '1em',
    },
    star: {
        position: 'absolute',
        top: 0,
        right: 0,
        marginTop: '5px',
        marginRight: '5px'
    },
    delete: {
        position: 'absolute',
        bottom: 0,
        right: 0,
        marginBottom: '5px',
        marginRight: '5px'
    }
})
const AllNews = () => {
    const classes = useStyle()
    const history = useHistory()
    const [state, setState] = useState({
        page: 1,
        type_news: 1,
        list: []
    })
    useEffect(() => {
        get_data(BACK_END_URL + 'public/news', {
            page: 1,
            item_per_page: 30,
            type_news: state.type_news,
        }, false)
            .then(res => {
                setState({
                    ...state,
                    list: res.data.list
                })
            })
            .catch(error => alert('Cannot load news!!!'))
    }, [state.type_news])
    const handleChange = (e) => {
        let val = e.target.value
        setState({
            ...state,
            type_news: val
        })
    }
    const refreshList = ()=> {
        get_data(BACK_END_URL + 'public/news', {
            page: 1,
            item_per_page: 30,
            type_news: state.type_news,
        }, false)
            .then(res => {
                setState({
                    ...state,
                    list: res.data.list
                })
            })
            .catch(error => alert('Cannot load news!!!'))
    }
    const [openDeleteDialog, setOpenDeleteDialog] = useState(false)
    const [curNews, setCurNews] = useState(0)
    const deleteNews = () => {
        delete_data(BACK_END_URL + `admin/news/${curNews}`, {}, true)
            .then(res => {
                setOpenDeleteDialog(false)
                refreshList()
            })
            .catch(error => alert("Error when delete"))
    }
    return (
        <div className={classes.root}>
            <h2>Tin tức</h2>
            <FormControl className={classes.formControl}>
                <Select
                    value={state.type_news}
                    onChange={handleChange}
                    className={classes.selectType}
                    variant={'outlined'}
                    name='type_news'
                >
                    <MenuItem value={0}>
                        Tin tức
                    </MenuItem>
                    <MenuItem value={1}>Thông tin chung</MenuItem>
                    <MenuItem value={2}>Hỗ trợ</MenuItem>
                </Select>
            </FormControl>
            <div className={classes.newContainer}>
                {
                    state.list.map((item, index) => (
                        <Paper onClick={() => {
                            history.push(`/edit_news/${item.path}`)
                        }} className={classes.elementContainer}>
                            <div>
                                <div className={classes.imageContainer} style={{backgroundImage: `url(${item.image})`}}>
                                    <div className={classes.innerText}>1</div>
                                    <div className={classes.star}>
                                        <IsHotNews isNew={item.is_new} news_id={item.id}/>
                                    </div>
                                    <div className={classes.delete}>
                                        <DeleteForeverIcon fontSize={'large'} onClick={
                                            (e) => {
                                                e.stopPropagation()
                                                setCurNews(item.id)
                                                setOpenDeleteDialog(true)
                                            }} color={'primary'}/>
                                    </div>
                                </div>
                                <div className={classes.wrapper}>
                                    <div className={classes.title}>{item.title}</div>
                                    <div className={classes.summary}>{item.summary}</div>
                                </div>
                            </div>
                        </Paper>
                    ))
                }
            </div>
            <DeleteDialog open={openDeleteDialog} onClose={() => setOpenDeleteDialog(false)}
                          handleDelete={deleteNews}/>
        </div>
    );
};

export default AllNews;