import React, {useEffect, useState} from 'react';
import StarIcon from "@material-ui/icons/Star";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import {makeStyles} from "@material-ui/core/styles";
import {delete_data, put_data} from "../../ApiCall";
import {BACK_END_URL} from "../../ServiceURL";
const useStyle = makeStyles({
    star : {
        color: "yellow",
    }
})
const IsHotNews = ({isNew, news_id}) => {
    const classes = useStyle()
    const [is_new, setNew] = useState(isNew)
    useEffect(()=>{
        setNew(isNew)
    },[isNew])
    const handleNew = (e)=>{
        e.stopPropagation()
        put_data(BACK_END_URL + `admin/news/${news_id}/is_new`, {}, {}, true)
            .then(res=> {setNew(true)})
            .catch(error=>console.log(error))
    }
    const handleUnNew = (e)=>{
        e.stopPropagation()
         delete_data(BACK_END_URL + `admin/news/${news_id}/is_new`, {}, {}, true)
             .then(res => {setNew(false)})
             .catch(error => console.log(error))
    }
    return (
        <div>
            {
                is_new?
                <div onClick={handleUnNew}>
                    <StarIcon className={classes.star} fontSize={'large'}/>
                </div>
                :
                <div onClick={handleNew}>
                <StarBorderIcon className={classes.star} fontSize={'large'}/>
                </div>
            }
        </div>
    );
};

export default IsHotNews;